body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

h1 {
  text-align: center;
}

.search-form {
  margin-bottom: 20px;
  display: flex;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  padding: 10px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #e0bc45;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.photo-card {
  width: calc(50% - 10px);
  margin-bottom: 20px;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
}

.photo-card img {
  max-width: 100%;
  height: auto;
}

.info {
  margin-top: 10px;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #0d2b4b;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  display: none;
}

.load-more.show {
  display: block;
}

